import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { Router } from "@angular/router";
import { Subject } from "rxjs";
import { environment } from "src/environments/environment";
import { ErrorBarComponent } from "../components/error-bar/error-bar.component";
import { AppUser, AppUserLogin, loggedUserDummy } from "../models/user.model";


const BASE_URL = environment.apiUrl + 'user/';

@Injectable()
export class AuthService{


    logInUserId: string;
    token: string;
    tokenTimer: any;
    private authStatusListener = new Subject<boolean>();

    private loggedInUser = new Subject<AppUser>();
    isAuthenticated: boolean = false;
    loggedUserData;


    constructor(private http: HttpClient, private router: Router, public dialog: MatDialog){

    }

    getToken(){
        return this.token;
    }

    getAuthStatusListener(){
        return  this.authStatusListener.asObservable();
    }

    getIsAuth(){
        return this.isAuthenticated;
    }

    getLoggedInUserListener(){
        return this.loggedInUser.asObservable();
    }

    getLoggedInUser(){
        return this.loggedUserData;

        // return loggedUserDummy;
    }


    createUser(userName: string, contact: string, email: string, pass: string){
        const userData: AppUser ={
            name: userName,
            contact: contact,
            email: email,
            password: pass
        }
        this.http.post( BASE_URL +'signup', userData)
        .subscribe(response => {
            this.router.navigate(['/login']);
        }, error => {
            this.authStatusListener.next(false);
        })
    }


    logIn(email: string, pass: string){
        this.loggedUserData = loggedUserDummy;
        this.isAuthenticated = true;
         this.authStatusListener.next(true);

         this.router.navigate(['/treatments']);
        // const authData: AppUserLogin ={
        //     email: email,
        //     password: pass
        // }
        // this.http.post<{message: string, token: string, expiresIn: number, user: AppUser}>(BASE_URL + 'login', authData)
        // .subscribe(response => {
        //     console.log(response);
        //     const token = response.token
        //     this.token = token;
        //     if(token){
        //         const expiresIn = response.expiresIn;
        //         this.loggedUserData = response.user;
        //         this.logInUserId = this.loggedUserData._id;
        //         this.setAuthTimer(expiresIn);
        //         this.isAuthenticated = true;
        //         this.loggedInUser.next(response.user);
        //         this.authStatusListener.next(true);
        //         const now = new Date();
        //         const exDate = new Date(now.getTime() + expiresIn * 1000);
        //         console.log(exDate);
        //         this.saveAuthData(token, exDate, this.loggedUserData._id);
        //         this.router.navigate(['/products']);
        //     }
        // }, error => {
        //     this.authStatusListener.next(false);
        // })
    }

    getLoggedUserInfo(uId: string){
        this.http.get<{user: AppUser}>( BASE_URL +'userInfo/' + uId)
        .subscribe(response => {
        //    this.loggedUserData = response.user;
        //    this.logInUserId = this.loggedUserData._id;
        //    this.loggedInUser.next(this.loggedUserData);
           this.authStatusListener.next(true);
        }, error => {
            this.authStatusListener.next(false);
        })
    }

    autoAuthUser(){
        const authInfo = this.getAuthData();
        if(!authInfo){
            return;
        }
        const now = new Date();
        const expiresIn = authInfo.expirationDate.getTime() - now.getTime();
        if(expiresIn > 0){
            this.token = authInfo.token;
            this.isAuthenticated = true;
            this.logInUserId = authInfo.userId;
            this.authStatusListener.next(true);
            
            this.getLoggedUserInfo(authInfo.userId);
            this.setAuthTimer(expiresIn/1000);
        }
    }


    logout(){
        this.token = null;
        this.isAuthenticated = false;
        this.authStatusListener.next(false);
        this.router.navigate(['/treatments']);
        this.clearAuthData();
        this.loggedUserData = null;
        clearTimeout(this.tokenTimer);
    }

    private setAuthTimer(duration){
        this.tokenTimer = setTimeout(() => {
            this.logout();
        }, duration * 1000);
    }

    private saveAuthData(token: string, expirationDate: Date, userId: string){
        localStorage.setItem('token', token);
        localStorage.setItem('expiration', expirationDate.toISOString());
        localStorage.setItem('userId', userId);
    }

    private clearAuthData(){
        localStorage.removeItem('token');
        localStorage.removeItem('expiration');
        localStorage.removeItem('userId');
    }

    private getAuthData(){
        let token = localStorage.getItem('token');
        let expDate = localStorage.getItem('expiration');
        let userId = localStorage.getItem('userId');

        if(!token || !expDate){
            return;
        }
        return {
            token: token,
            expirationDate: new Date(expDate),
            userId: userId
        };
    }

    updateUserProfile(user: AppUser){

        this.http.put<{message: string, user: AppUser}>(BASE_URL + 'updateUser/'+ user._id, user)
        .subscribe(response => {
            console.log(response)
            this.loggedInUser.next(response.user);
            this.authStatusListener.next(true);

            const dialogRef = this.dialog.open(ErrorBarComponent, { panelClass: 'alertDialog',
                    data: {message: response.message, type: 'success'}, hasBackdrop: true, backdropClass:'default'
                })

                setTimeout(function() {
                    dialogRef.close();
                }.bind(this), 2000);
        }, error => {
            console.log(error);
        })
    }


}